@tailwind base;
@tailwind components;
@tailwind utilities;

/* Custom Scroll-bar */

/* For transparent background of scroll-bar */
body {
    overflow: overlay;
}

/* width */
::-webkit-scrollbar {
    width: 5px;
    height: 5px;
}

/* Handle */
::-webkit-scrollbar-thumb {
    background: rgb(68, 68, 68);
    border-radius: 0.1rem;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
    background: #555;
}

::-webkit-scrollbar-corner {
    background: rgba(0, 0, 0, 0.5);
}


::selection{color:#20436d;background:#b4d5e090}